html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: #504f4f;
  font-family: "Libre Bodoni", serif;
}

h1,
h2,
h3 {
  line-height: 1.1;
  margin: 0;
  font-weight: 700;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  cursor: pointer;
}

#toggleTheme {
  border: none;
  background: none;
}

.icon {
  cursor: pointer;
  font-size: 2rem;
  padding: 0.5em;
}

.fb {
  color: #0866ff;
}

.in {
  color: #0a66c2;
}